"use client";

import { useEffect, useRef, useState } from "react";
import gsap from "gsap";

import projects from "../../data/projects";
import { Tag } from "../../components/tag";

const allTags = Array.from(new Set(projects.flatMap((project) => project.tag)));
const selectedTags = ["UIUX Design"];

const filteredProjects = projects.filter((project) =>
  selectedTags.some((tag) => project.tag.includes(tag))
);

export default function Work() {
  const headingRef = useRef<HTMLDivElement>(null);
  const paragraphRef = useRef<HTMLDivElement>(null);

  const [minH, setMinH] = useState("100vh");

  // Get navbar height
  useEffect(() => {
    const updateMinHeight = () => {
      const nav = document.getElementById("navbar");
      const h = nav?.offsetHeight ?? 0;
      setMinH(`calc(100vh - ${h}px)`);
    };
    updateMinHeight();
    window.addEventListener("resize", updateMinHeight);
  }, []);

  useEffect(() => {
    if (!headingRef.current || !paragraphRef.current) return;
    if (window.matchMedia("(prefers-reduced-motion: reduce)").matches) return;

    const ctx = gsap.context(() => {
      gsap.to(headingRef.current, {
        filter: "blur(20px)",
        x: () => -window.innerWidth * 0.4,
        opacity: 0.5,
        ease: "power2.out",
        scrollTrigger: {
          trigger: headingRef.current,
          start: "top 50%",
          end: "bottom top",
          scrub: true,
        },
      });

      gsap.to(paragraphRef.current, {
        filter: "blur(20px)",
        x: () => window.innerWidth * 0.4,
        opacity: 0.5,
        ease: "power2.out",
        scrollTrigger: {
          trigger: paragraphRef.current,
          start: "top 50%",
          end: "bottom top",
          scrub: true,
        },
      });
    });

    return () => ctx.revert();
  }, []);

  return (
    <div className="py-40 px-10 md:px-30">
      <section>
        <div className="grid grid-cols-1 md:grid-cols-2">
          <div className="font-telugu font-bold text-5xl">
            <h2>Works.</h2>
          </div>
          <div className="font-montserrat leading-[200%] flex justify-end items-start">
            <p>
              A showcase of my work across diverse fields, including UI/UX
              design, web development, no-code solutions, and graphic design. I
              continuously learn new skills and tools, staying adaptable and
              open to new possibilities.
            </p>
          </div>
        </div>
      </section>
      <section>
        <div className="inline-flex gap-4">
          {allTags.map((item, index) => (
            <Tag variant="bordered" colour="foreground" className="active:bg-cta active:text-background hover:bg-foreground/20">{item}</Tag>
          ))}
        </div>
      </section>
    </div>
  );
}
